import React from 'react';
import './Newsletter.css';


const Newsletter = () => {
return( 
        <section className="newsletter">
            <div className="container">
                <div className="col-md-6">
                    <div className="bloco-newsletter">
                        <h1>Join our membership<br/>to get special offer</h1>

                        <form class="row g-3 d-flex-center">
                            <div class="col-7">
                                <input type="password" class="form-control form" id="inputPassword2" placeholder="Enter your email address"/>
                            </div>
                            <div class="col-auto">
                                <button type="submit" class="btn btn-newsletter mb-3">
                                    Join
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Newsletter;