import React from 'react';
import './home.css';

const Home = () => {
  return( 
    <div>
      <h1>Ready for
      <br/>
      Trying a new 
       <br/> recipe?</h1>

       <form class="row g-3">
        <div class="col-7">
          <input type="password" class="form-control form" id="inputPassword2" placeholder="Search healthy recipes"/>
        </div>
        <div class="col-auto">
          <button type="submit" class="btn btn-color mb-3">
            <i class="ti-search"></i>
          </button>
        </div>
      </form>
    </div>
    
 
    
      

      
    );
};

export default Home;