import React from 'react';
import './Recipes.css';

import Comida1 from '../../assets/img/comida_1.svg'
import Comida2 from '../../assets/img/comida_2.svg'
import Comida3 from '../../assets/img/comida_3.svg'
import Comida4 from '../../assets/img/comida_4.svg'


const Recipes = () => {
  return( 
    <section>
        <div className="container">
            <div className="text-center titulo">
                <h2>Our Best Recipes</h2>
                <h5>Far far away, behind the word mountains,  far from the countries <br/> Vokalia and Consonantia, there live the blind texts.</h5>
            </div>
        </div>
        <div className="container">
            <aside className="grid-card-recipes">
                <div className="card-recipes">
                    <div>
                        <img src={Comida1} className="img-fluid"/>
                    </div>
                    <div className="pr10">
                        <h4>Broccoli Salad <br/> with Bacon</h4>
                        <button className="btn btn-recipes">See Recipe</button>
                    </div>
                </div>
                <div className="card-recipes">
                    <div>
                    <img src={Comida2} className="img-fluid"/>
                    </div>
                    <div className="pr10">
                        <h4>Classic Beef  <br/> Burgers </h4>
                        <button className="btn btn-recipes">See Recipe</button>
                    </div>
                </div>
                <div className="card-recipes">
                    <div>
                    <img src={Comida3} className="img-fluid"/>
                    </div>
                    <div className="pr10">
                        <h4>Classic Potato <br/> Salad</h4>
                        <button className="btn btn-recipes">See Recipe</button>
                    </div>
                </div>
                <div className="card-recipes">
                    <div>
                    <img src={Comida4} className="img-fluid"/>
                    </div>
                    <div className="pr10">
                        <h4> Cherry Cobbler <br/> on the Grill</h4>
                        <button className="btn btn-recipes">See Recipe</button>
                    </div>
                </div>
            </aside>
        </div>
    </section>
    );
};

export default Recipes;