import React from 'react';
import Home from '../Home/home';
import './reader.css';
import { Link } from 'react-router-dom';
const Reader = () => {
  
  function handleClick(){
    let linha1 = document.getElementById('linha-1')
    linha1.classList.toggle('linha1')

    let linha2 = document.getElementById('linha-2')
    linha2.classList.toggle('linha2')  

    let menu = document.getElementById('menu')
    menu.classList.toggle('active-menu')
    
    let body = document.body
    body.classList.toggle('overflow-hidden')
  } 
  
  return( 
      <section className="imgHome">
        
        <div id="home1">
          <div className="container padding">
            <div className="cabecalho">
                <div>
                      <div className="logo">
                        <span>Healthy Food</span>
                          <div>
                              <div className="menu-movibile" id="abrir" onClick={handleClick}>
                                  <div id="linha-1" className="linha-movibile"></div>
                                  <div id="linha-2" className="linha-movibile"></div>
                              </div>
                          </div>
                    </div>
                  </div>

                  <nav className="menu">
                      <ul id='menu'>
                          <li><a href="">HEALTHY RECIPES</a></li>
                          <li><a href="">BLOG</a></li>
                          <li><a href="">JOIN</a></li>
                          <li><a href="">REGISTER</a></li>
                      </ul>
                  </nav>
            </div>
          </div>
        </div>
        <section>
          <div className="container">
            <div className="col-md-6">
              <Home/>
            </div>
          </div>
        </section>
        
      </section>  
    );
};

export default Reader;