import React from 'react';
import './blog.css';

import OwlCarousel from 'react-owl-carousel'
import 'owl.carousel/dist/assets/owl.carousel.min.css'
import 'owl.carousel/dist/assets/owl.theme.default.min.css'

import bloco_img1 from '../../assets/img/blog_image_1.svg'
import bloco_img2 from '../../assets/img/bloco_image_2.png'
import bloco_img3 from '../../assets/img/bloco_image_3.png'
import bloco_img4 from '../../assets/img/bloco_image_4.png'

import foto1 from '../../assets/img/Photo1.png'
import foto2 from '../../assets/img/Photo2.png'
import foto3 from '../../assets/img/Photo3.png'
import foto4 from '../../assets/img/Photo4.png'

const Blog = () => {
    const options = {
        margin: 30,
        responsiveClass: true,
        nav: true,
        dots: false,
        autoplay: true,
        navText: ["Prev",' <i class="ti-arrow-right"></i>'],
        responsive: {
            0: {items: 1,},
            400: {items: 1,},
            600: {items: 2,},
            700: {items: 2,},
            800: {items: 2,},
            900: {items: 3,},
            1100: { items: 4,}
        },
    };
return( 
        <section>
            <div className="container">
                <div className="text-center titulo-blog">
                    <h2>Read Our Blog</h2>
                    <h5>Far far away, behind the word mountains,  far from the <br/> countries Vokalia and Consonantia, there live the blind texts.</h5>
                </div>
            </div>
            <div className="container">
                <OwlCarousel {...options}>

                    <div className="card-blog item">
                        <img src={bloco_img1} className="img-fluid" />
                        <div className="p-3">
                            <h4 className="titulo">Quick-start guide <br/> to nuts and seeds</h4>
                            <div className=" d-flex">
                                <img src={foto1} className="foto-autor"/>
                                <span>Kevin Ibrahim</span>
                            </div>
                        </div>
                    </div>
                    <div className="card-blog item">
                        <img src={bloco_img2} className="img-fluid"/>
                        <div className="p-3">
                            <h4 className="titulo">Nutrition: Tips for <br/> Improving Your Health</h4>
                            <div className="d-flex">
                                <img src={foto2} className="foto-autor"/>
                                <span>Mike Jackson</span>
                            </div>
                        </div>
                    </div>
                    <div className="card-blog item">
                        <img src={bloco_img3} className="img-fluid"/>
                        <div className="p-3">
                            <h4 className="titulo">The top 10 benefits <br/> of eating healthy</h4>
                            <div className=" d-flex">
                                <img src={foto3} className="foto-autor"/>
                                <span>Bryan McGregor</span>
                            </div>
                        </div>
                        
                    </div>
                    <div className="card-blog item">
                        <img src={bloco_img4} className="img-fluid"/>
                        <div className="p-3">    
                            <h4 className="titulo">What Makes a <br/> Healthy Diet?</h4>
                            <div className=" d-flex ">
                                <img src={foto4} className="foto-autor"/>
                                <span>Jashua</span>
                            </div>
                        </div>
                    </div>
                    
                </OwlCarousel>
            </div>
        </section>
    );
};

export default Blog;